
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-active {
  opacity:0;
}
.slide-enter-active, .slide-leave-active {
  transition: transform .5s, opacity .5s;
}
.slide-enter {
  transform: translateX(-200px);
  opacity: 0;
}
.slide-leave-active {
  transform: translateX(200px);
  opacity: 0;
}




